.about-products {
  background-color: #E4E4E4;
  padding-bottom: 3.5%;
}

.about-products[category="omnilife"] .wrapper .title,
.about-products[category="omnilife"] .wrapper .question {
  color: var(--bg-darkpurple);
}

.about-products[category="seytu"] .wrapper .title,
.about-products[category="seytu"] .wrapper .question {
  color: var(--bg-darkred);
}

.about-products[category="omnilife"] .wrapper .see-all-products {
  background-color: var(--bg-darkpurple);
}

.about-products[category="omnilife"] .wrapper .see-all-products:hover {
  background-color: #A2008E !important;
}

.about-products[category="seytu"] .wrapper .see-all-products {
  background-color: var(--bg-darkred);
}

.about-products[category="seytu"] .wrapper .see-all-products:hover {
  background-color: #C00040 !important;
}

.about-products > .wrapper {
  width: 75%;
  margin: auto;
  padding-bottom: 2%;
}

.about-products .wrapper .title {
  margin-bottom: 3%;
  font-size: 4em;
  padding-bottom: 15px;
  text-align: center;
  line-height: normal;
  border-bottom: 1px solid rgba(0,0,0,0.15);
}

.about-products .wrapper .title,
.about-products .wrapper .question {
  font-weight: bold;
}

.about-products .wrapper figure {
  width: 70%;
  margin: auto;
  margin-top: 3%;
}

.about-products .wrapper .question {
  margin: 25px 0;
  font-size: 2.6em;
  line-height: normal;
}

.about-products .wrapper .response {
  font-size: 1.45em;
  white-space: pre-wrap;
  margin: 0;
}

.about-products .wrapper .see-all-products {
  border: none;
  border-radius: 5px;
  color: var(--bg-white);
  display: table;
  margin: auto;
  margin-top: 30px;
  padding: 10px 80px;
  cursor: pointer;
  font-size: 1.3em;
  font-family: 'Electrolize', sans-serif;
}

.about-products .products {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
  overflow: hidden;
}

/**
 * Media queries
 */

@media (max-width: 1280px) {
  .about-products .products {
    width: 90%;
  }
  .about-products .wrapper .title {
    font-size: 3.5em;
  }
}

@media (max-width: 1080px) {
  .about-products .products {
    width: 100%;
  }
  .about-products .products .product {
    width: 25%;
  }
}

@media (max-width: 1024px) {
  .about-products > .wrapper {
    width: 85%;
  }
  .about-products .wrapper .response {
    font-size: 1.35em;
  }
  .about-products .wrapper .see-all-products {
    font-size: 1.25em;
  }
  .about-products .products .product {
    box-shadow: 0 1px 9px 1px rgb(0 0 0 / 25%);
  }
  .about-products .products .product:last-of-type {
    display: none;
  }
}

@media (max-width: 875px) {
  .about-products {
    padding: 0 0 50px;  
  }
  .about-products > .wrapper figure {
    width: 100%;
    margin: 20px 0;
  }
  .about-products .products .product {
    width: 30%;
  }
}

@media (max-width: 700px) {
  .about-products .products .product {
    width: 40%;
  }
  .about-products .products .product:last-of-type {
    display: flex;
  }
}

@media (max-width: 635px) {
  .about-products .products .product {
    width: 45%;
  }
}

@media (max-width: 480px) {
  .about-products[category="omnilife"] {
    padding-top: 20% !important;
  }
  .about-products[category="seytu"] {
    padding-top: 10% !important;
  }
  .about-products > .wrapper {
    width: 90%;
    padding-bottom: 10%;
  }
  .about-products > .wrapper .response {
    font-size: 1.25em;
  }
  .about-products > .wrapper .see-all-products {
    padding: 12px 0;
    font-size: 1rem;
    display: block;
    text-align: center;
    width: 100%;
  }
  .about-products .products {
    width: 100%;
  }
  .about-products .products .product {
    width: 50%;
    margin: 0;
    box-shadow: none;
    border-radius: 0%;
    border-top: 1px solid rgba(0,0,0,0.15);
  }
  .about-products .products .product:nth-child(even) {
    border-left: 1px solid rgba(0,0,0,0.15);
  }
  .about-products .products .product:nth-last-of-type(-n+2) {
    border-bottom: 1px solid rgba(0,0,0,0.15);
  }
}

@media (max-width: 375px) {
  .about-products .products .product {
    width: 70%;
    margin-bottom: 15px;
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,0.15);
  }
  .about-products .products .product:last-child {
    margin-bottom: 0;
  }
}

@media (max-width: 360px) {
  .about-products .products .product {
    width: 80%;
  }
}

@media (max-width: 320px) {
  .about-products, .about-products > .wrapper .see-all-products {
    font-size: 4.25vw;
  }
}

@media (max-width: 280px) {
  .about-products .products .product {
    width: 90%;
  }
}