/* ----- Background Header ----- */

header {
  background: url('@assets/img/header/background-header.webp');
  background-repeat: no-repeat;
  background-size: 100%;
}

header .logo-omnilife {
  width: 70%;
}

/* ----- Navigation ----- */

#navigation {
  box-sizing: border-box;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  background: url('@assets/img/header/background-nav.webp');
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
  width: 100%;
  padding: 0.5% 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: initial;
}

#logo-seytu {
  width: 12%;
}

#navigation .navigation-item-wrapper {
  margin: 0;
  padding: 0;
}

#navigation .navigation-item-wrapper .navigation-item {
  display: inline-block;
  border-radius: 30px;
}

#navigation .navigation-item-wrapper .navigation-item:hover {
  background-color: var(--bg-nav);
  color: var(--bg-yellow);
}

#navigation .navigation-item, .navigation-item {
  font-family: 'Rubik', sans-serif;
}

#navigation .navigation-item-wrapper .navigation-item .link {
  padding: 10px 20px;
  color: inherit;
  text-decoration: none;
}

.navigation-item .link {
  font-size: 15px;
}

.link, .link:active {
  color: #000;
  display: block;
  padding: 5px 15px;
}

.link:hover {
  color: var(--bg-nav);
}

@media (max-width: 1024px) {
  #navigation {
    border: none;
    position: fixed;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.5);
    border-bottom: 2px solid;
    border-bottom-color: black;
    border-bottom-color: rgba(0, 0, 0, 0.5);
    font-size: 17px;
    z-index: 60;
    top: 0;
    width: 100%;
    padding: 0 5%;
    height: 80px;
  }
  #navigation .navigation-item-wrapper {
    border-top: 2px solid;
    border-top-color: black;
    border-top-color: rgba(0, 0, 0, 0.85);
    position: absolute;
    top: 100%;
    width: 500px;
    background: var(--bg-main);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    font-size: 0.8em;
    font-weight: bold;
    border-bottom: 3px solid black;
    height: 100vh;
    right: 0;
    transform: translateX(100%);
    transition: transform 0.7s linear 0s;
    will-change: transform;
  }
  #navigation .navigation-item-wrapper.open {
    transform: translateX(0%);
  }
  #navigation .navigation-item-wrapper .navigation-item {
    display: block;
    padding: 10px 20px 10px 0;
    color: black;
    border-bottom: 1px solid;
    border-bottom-color: black;
    border-bottom-color: rgba(0, 0, 0, 0.25);
    border-radius: 0;
    font-weight: normal;
    font-size: 1.25em;
  }
  #navigation .ant-collapse-content-box .navigation-item:last-of-type {
    border-bottom: none;
  }
  #navigation .navigation-item-wrapper .navigation-item:hover {
    box-shadow: 3px 0 0 0 var(--bg-yellow) inset;
  }
  #navigation .navigation-item-wrapper figure {
    width: 35%;
    display: block;
    margin: 50px auto 0;
  }
  #navigation .ant-collapse-header {
    padding: 0;
  }
  #navigation .ant-collapse {
    background-color: inherit;
  }
  #navigation .ant-collapse-content > .ant-collapse-content-box {
    padding: 0 16px;
  }
  .navigation-item .link {
    font-size: 17px;
  }
  #logo-omnilife {
    width: 100%;
  }
  #menu, #close-menu {
    font-size: 2.5em;
  }
  #logo-seytu {
    width: 125px;
    height: auto
  }
}

@media (max-width: 768px) {
  #navigation {
    padding: 1.5% 5% 1.5% 3%;
    height: 60px;
  }
  .navigation-item .link {
    font-size: 16px;
  }
  #logo-seytu {
    width: 100px;
  }
  #menu, #close-menu {
    font-size: 2em;
  }
}

@media (max-width: 500px) {
  #navigation .navigation-item-wrapper {
    width: 100%;
  }
}

@media (max-width: 360px) {
  #logo-seytu {
    width: 30vw;
  }
}

@media (max-width: 280px) {
  #navigation {
    height: 20vw;
  }
  #navigation .navigation-item-wrapper .navigation-item,
  #navigation .navigation-item-wrapper .navigation-item .link {
    padding: 3%
  }
  .navigation-item .link {
    font-size: 6vw;
  }
  #menu, #close-menu {
    font-size: 12vw;
  }
}