.about-omnilife {
  padding: 7% 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  background-color: black;
  color: var(--bg-white);
}

.about-omnilife-wrapper {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: auto;
}

.about-omnilife-wrapper .info {
  width: 45%;
  line-height: normal;
}

.about-omnilife-wrapper .info .title {
  font-size: 3.35em;
  color: var(--bg-white);
  font-weight: bold;
}

.about-omnilife-wrapper .info .description {
  font-size: 1.45em;
  line-height: 1.5;
}

.about-omnilife-wrapper .info .more-information {
  display: table;
  border-radius: 3px;
  padding: 2.5% 10%;
  font-weight: 500;
  color: var(--bg-white);
  font-size: 1.3em;
  margin-top: 1.5%;
  margin-bottom: 5%;
  font-family: 'Rubik', sans-serif;
}

.about-omnilife-wrapper .info .more-information:hover {
  opacity: .6;
}

.about-omnilife-wrapper figure {
  width: 40%;
}

/**
 * Media queries
 */

@media (max-width: 1100px) {
  .about-omnilife-wrapper {
    width: 90%;
  }
}

@media (max-width: 1024px) {
  .about-omnilife {
    background-attachment: scroll;
  }
}

@media (max-width: 980px) {
  .about-omnilife-wrapper .info {
    width: 50%;
  }
}

@media (max-width: 885px) {
  .about-omnilife-wrapper {
    width: 95%;
  }
}

@media (max-width: 835px) {
  .about-omnilife-wrapper .info {
    width: 60%;
    margin-bottom: 25px;
  }
  .about-omnilife-wrapper figure {
    width: 60%;
  }
}

@media (max-width: 768px) {
  .about-omnilife-wrapper .info {
    width: 500px;
  }
}

@media (max-width: 615px) {
  .about-omnilife-wrapper .info {
    width: 80%;
  }
}

@media (max-width: 500px) {
  .about-omnilife:first-of-type {
    padding: 50px 0
  }
  .about-omnilife-wrapper .info {
    width: 85%;
  }
  .about-omnilife-wrapper .info:first-of-type {
    width: 90%;
  }
  .about-omnilife-wrapper .info .title {
    font-size: 3em;
  }
  .about-omnilife-wrapper .info .description {
    font-size: 1.35em;
  }
  .about-omnilife-wrapper .info .more-information {
    padding: 15px 20px;
    width: 80%;
    text-align: center;
  }
}

@media (max-width: 320px) {
  .about-omnilife-wrapper .info {
    width: 90%;
  }
  .about-omnilife-wrapper .info .title {
    font-size: 13vw;
  }
  .about-omnilife-wrapper .info .description {
    font-size: 6vw;
  }
  .about-omnilife-wrapper .info .more-information {
    font-size: 5.5vw;
    padding: 5vw 10vw;
  }
}