.testimonials {
  background-color: var(--bg-main);
  padding: 20px 0 70px;
  /* padding-bottom: 5%; */
}

.testimonials .wrapper {
  width: 90%;
  margin: auto;
}

.testimonials .wrapper .title {
  text-align: center;
  font-size: 4.75em;
  padding: 10px 0;
  font-family: "IM Fell English SC", sans-serif;
  line-height: normal;
  border-bottom: 1px solid rgba(0, 0, 0, .15);
  width: 85%;
  line-height: 1;
  margin: 3.5% auto;
}

.testimonials .wrapper .testimonials-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/**
 * Media queries
 */

@media (max-width: 1024px) {
  .testimonials .wrapper {
    width: 100%;
  }
  .testimonials .wrapper .title {
    text-align: center;
    font-size: 4.5em;
  }
  .testimonials .wrapper .testimonials-content .testimony .author-testimony {
    font-size: 1.1em;
  }
}

@media (max-width: 900px) {
  .testimonials .wrapper .title {
    width: 90%;
  }
  .testimonials .wrapper .testimonials-content .testimony {
    width: 40%;
    padding: 30px 0;
  }
}

@media (max-width: 768px) {
  .testimonials .wrapper .testimonials-content .testimony {
    width: 45%;
  }
}

@media (max-width: 600px) {
  .testimonials .wrapper .title {
    text-align: center;
    font-size: 4em;
  }
  .testimonials .wrapper .testimonials-content .testimony {
    width: 85%;
    margin: 12px 0;
  }
}

@media (max-width: 400px) {
  .testimonials .wrapper .testimonials-content .testimony {
    width: 90%;
  }
}

@media (max-width: 360px) {
  .testimonials .wrapper {
    font-size: 4vw;
  }
}
